<template>
  <div class="asp">
    <div class="asp-nav"></div>
    <div class="asp-content">
      <div class="asp-form">
        <el-form :inline="true" :model="formInline" class="demo-form-inline">
          <el-form-item label="">
            <el-select placeholder="">
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
          </el-form-item>
          <el-form-item style="float:right;margin:0 90px 0 0">
            <el-button type="primary" @click="dialogVisible = true"></el-button>
          </el-form-item>
        </el-form>
        <div class="asp-table">
          <el-table
            :data="tableData"
            tooltip-effect="dark"
            style="width: 100%"
            @selection-change="handleSelectionChange"
          >
            <el-table-column prop="name" label="ID" width="120"></el-table-column>
            <el-table-column prop="date" label="" width="120"></el-table-column>
            <el-table-column prop="img" label="" show-overflow-tooltip>
              <template>
                <el-image
                  style="width: 100px; height: 100px"
                  :src="this.url"
                  :preview-src-list="srcList"
                ></el-image>
              </template>
            </el-table-column>
            <el-table-column prop="address" label="" show-overflow-tooltip></el-table-column>
            <el-table-column label="()">
              <template slot-scope="scope">
                <el-button size="mini" type="text" @click="sortUp(scope.$index, scope.row)">
                  <i style="font-size:20px" class="el-icon-arrow-up"></i>
                </el-button>
                <el-button size="mini" type="text" @click="sortDown(scope.$index, scope.row)">
                  <i style="font-size:20px" class="el-icon-arrow-down"></i>
                </el-button>
              </template>
            </el-table-column>
            <el-table-column prop="address" sortable label="" show-overflow-tooltip></el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <el-button @click="handleClick(scope.row)" type="text" size="small"></el-button>
                <el-button type="text" size="small"></el-button>
              </template>
            </el-table-column>
          </el-table>
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            :page-sizes="[100, 200, 300, 400]"
            :page-size="100"
            layout="total, sizes, prev, pager, next, jumper"
            :total="400"
          ></el-pagination>
        </div>
      </div>
    </div>
    <el-dialog title="" :visible.sync="dialogVisible" width="30%" :before-close="handleClose">
      <el-form ref="form" :model="form" label-width="80px" :rules="rules">
        <el-form-item label="" prop="name">
          <el-input v-model="form.name" style="width:300px"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-upload
            class="avatar-uploader"
            action="https://jsonplaceholder.typicode.com/posts/"
            :show-file-list="false"
            :on-success="handleAvatarSuccess"
            :before-upload="beforeAvatarUpload"
          >
            <img v-if="imageUrl" :src="imageUrl" class="avatar" />
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
          <div>100*100 px ()</div>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="form.region" @change="move" style="width:300px" placeholder="">
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
            <el-option label="URL" value="URL"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="ID" v-if="position" prop="position">
          <el-input v-model="form.position" style="width:300px"></el-input>
        </el-form-item>
        <el-form-item label="ID" v-if="company" prop="company">
          <el-input v-model="form.company" style="width:300px"></el-input>
        </el-form-item>
        <el-form-item label="" v-if="link" prop="link">
          <el-input v-model="form.link" style="width:300px"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button @click="dialogVisible = false"> </el-button>
          <el-button type="primary" @click="submitForm('form')"> </el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
    <el-dialog title="" :visible.sync="dialogVisibleOne" width="30%" :before-close="handleClose">
      <el-form ref="form" :model="form" label-width="80px" :rules="rules">
        <el-form-item label="" prop="name">
          <el-input v-model="form.name" style="width:300px"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-upload
            class="avatar-uploader"
            action="https://jsonplaceholder.typicode.com/posts/"
            :show-file-list="false"
            :on-success="handleAvatarSuccess"
            :before-upload="beforeAvatarUpload"
          >
            <img v-if="imageUrl" :src="imageUrl" class="avatar" />
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
          <div>100*100 px ()</div>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="form.region" @change="move" style="width:300px" placeholder="">
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
            <el-option label="URL" value="URL"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="ID" v-if="position" prop="position">
          <el-input v-model="form.position" style="width:300px"></el-input>
        </el-form-item>
        <el-form-item label="ID" v-if="company" prop="company">
          <el-input v-model="form.company" style="width:300px"></el-input>
        </el-form-item>
        <el-form-item label="" v-if="link" prop="link">
          <el-input v-model="form.link" style="width:300px"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button @click="dialogVisibleOne = false"> </el-button>
          <el-button type="primary" @click="submitFormOne('form')"> </el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      form: {
        name: "",
        region: "",
        position: "",
        company: "",
        link: ""
      },
      position: "",
      company: "",
      link: "",
      checkList: [" A"],
      dialogVisible: false,
      dialogVisibleOne:false,
      activeName: "first",
      url:
        "https://fuss10.elemecdn.com/e/5d/4a731a90594a4af544c0c25941171jpeg.jpeg",
      srcList: [
        "https://fuss10.elemecdn.com/8/27/f01c15bb73e1ef3793e64e6b7bbccjpeg.jpeg",
        "https://fuss10.elemecdn.com/1/8e/aeffeb4de74e2fde4bd74fc7b4486jpeg.jpeg"
      ],
      currentPage: 10,
      tableData: [
        {
          date: "2016-05-03",
          name: "123",
          address: " 1518 "
        },
        {
          date: "2016-05-02",
          name: "232",
          address: " 1518 "
        },
        {
          date: "2016-05-04",
          name: "3343",
          address: " 1518 "
        },
        {
          date: "2016-05-01",
          name: "dfd",
          address: " 1518 "
        },
        {
          date: "2016-05-08",
          name: "dfdf",
          address: " 1518 "
        },
        {
          date: "2016-05-06",
          name: "bvbv",
          address: " 1518 "
        },
        {
          date: "2016-05-07",
          name: "klk",
          address: " 1518 "
        }
      ],
      rules: {
        position: [
          { required: true, message: "ID", trigger: "blur" }
        ],
        company: [{ required: true, message: "ID", trigger: "blur" }],
        link: [{ required: true, message: "URL", trigger: "blur" }],
        name: [{ required: true, message: "", trigger: "blur" }]
      }
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
        this.dialogVisible = false
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    submitFormOne(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
        this.dialogVisibleOne = false
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    move() {
      if (this.form.region == "") {
        this.position = true;
        this.company = false;
        this.link = false;
      } else if (this.form.region == "") {
        this.position = false;
        this.company = true;
        this.link = false;
      } else if (this.form.region == "URL") {
        this.position = false;
        this.company = false;
        this.link = true;
      }
    },
    // 
    sortUp(index, row) {
      var that = this;
      console.log("", index, row);
      console.log(that.tableData[index]);
      if (index > 0) {
        let upDate = that.tableData[index - 1];
        that.tableData.splice(index - 1, 1);
        that.tableData.splice(index, 0, upDate);
      } else {
        alert("");
      }
    },
    // 
    sortDown(index, row) {
      var that = this;
      console.log("", index, row);
      if (index + 1 === that.tableData.length) {
        alert("");
      } else {
        console.log(index);
        let downDate = that.tableData[index + 1];
        that.tableData.splice(index + 1, 1);
        that.tableData.splice(index, 0, downDate);
      }
    },
    keep() {
      this.dialogVisible = true;
    },
    handleClick() {
      this.dialogVisibleOne = true;
    },
    toggleSelection(rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    handleSizeChange(val) {
      console.log(` ${val} `);
    },
    handleCurrentChange(val) {
      console.log(`: ${val}`);
    },
    handleAvatarSuccess(res, file) {
      this.imageUrl = URL.createObjectURL(file.raw);
    },
    beforeAvatarUpload(file) {
      const isJPG = file.type === "image/jpeg";
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isJPG) {
        this.$message.error(" JPG !");
      }
      if (!isLt2M) {
        this.$message.error(" 2MB!");
      }
      return isJPG && isLt2M;
    }
  },
  mounted: function() {},
  updated: function() {}
};
</script>
<style scoped>
.asp {
  width: 100%;
}
.asp-nav {
  margin: 10px 0 0 0;
  font-size: 17px;
  font-weight: 550;
}
.asp-content {
  width: 100%;
  margin: 20px 0 0 0;
  border: 1px solid #efeff3;
  background: #efeff3;
}
.asp-form {
  width: 98%;
  background: #ffffff;
  margin: 10px 0 0 1%;
}
.el-form-item {
  padding: 10px 0 0 20px;
}
.el-select {
  width: 203px;
}
.asp-table {
  width: 98%;
  background: #ffffff;
  margin: 10px 0 0 1%;
}
.el-pagination {
  margin: 20px 0 0 400px;
  padding: 0 0 20px 0;
}
.el-checkbox__label {
  font-size: 10px;
}
.el-checkbox {
  margin: 10px 0 0 20px;
}
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 100px;
  height: 100px;
  line-height: 100px;
  text-align: center;
}
.avatar {
  width: 100px;
  height: 100px;
  display: block;
}
</style>


