<template>
  <div class="asp">
    <div class="asp-nav"></div>
    <el-dialog
      title=""
      :visible.sync="dialogVisible"
      width="30%"
      :before-close="handleClose"
    >
      <div>
        <div style="margin:-40px 0 0 0"></div>
        <el-divider></el-divider>
        <el-input type="textarea" :rows="2" placeholder="" v-model="textarea"></el-input>
        <el-divider style="margin:0 0 0 0"></el-divider>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false"> </el-button>
          <el-button type="primary" @click="dialogVisible = false"> </el-button>
        </span>
      </div>
    </el-dialog>
    <div class="asp-content">
      <div class="asp-form">
        <el-form :inline="true" :model="formInline" class="demo-form-inline">
          <el-form-item label="">
            <el-input placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="ID">
            <el-input placeholder="ID"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-select placeholder="">
              <el-option label="" value="shanghai"></el-option>
              <el-option label="" value="beijing"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-select placeholder="">
              <el-option label="" value="shanghai"></el-option>
              <el-option label="" value="beijing"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item style="float:right;margin:0 90px 0 0">
            <el-button @click="resetForm('ruleForm')"></el-button>
            <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
          </el-form-item>
        </el-form>
        <div class="asp-table">
          <el-tabs v-model="activeName" type="card">
            <el-tab-pane label="" name="first">
              <el-table
                ref="multipleTable"
                :data="tableData"
                tooltip-effect="dark"
                style="width: 100%"
                @selection-change="handleSelectionChange"
              >
                <el-table-column type="selection" width="100"></el-table-column>
                <el-table-column prop="name" label="ID"></el-table-column>
                <el-table-column prop="name" label=""></el-table-column>
                <el-table-column prop="address" label="" show-overflow-tooltip></el-table-column>
                <el-table-column prop="address" label="" show-overflow-tooltip></el-table-column>
                <el-table-column prop="address" label="" show-overflow-tooltip></el-table-column>
                <el-table-column prop="address" sortable label="" show-overflow-tooltip></el-table-column>
                <el-table-column label="">
                  <template slot-scope="scope">
                    <el-button @click="handleClick(scope.row)" type="text" size="small"></el-button>
                    <el-button type="text" size="small"></el-button>
                    <el-tooltip class="item" effect="light" placement="bottom-start">
                      <div slot="content">
                        <span>
                          <i style="color:#faad14;margin:0 5px 0 0" class="el-icon-warning-outline"></i>
                        </span>
                        <span></span>
                        <div style="font-size:10px">
                          <el-checkbox-group v-model="checkList">
                            <el-checkbox label=""></el-checkbox>
                            <br />
                            <el-checkbox label=""></el-checkbox>
                            <br />
                            <el-checkbox label=""></el-checkbox>
                            <br />
                            <el-checkbox label=""></el-checkbox>
                            <br />
                            <el-checkbox label=""></el-checkbox>
                          </el-checkbox-group>
                          <br />
                          <el-button style="width:50px;height:20px;font-size:10px;padding:0 0"></el-button>
                          <el-button
                            style="width:50px;height:20px;font-size:10px;padding:0 0"
                            type="primary"
                          ></el-button>
                        </div>
                      </div>
                      <el-button type="text" size="small"></el-button>
                    </el-tooltip>
                  </template>
                </el-table-column>
              </el-table>
              <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="currentPage"
                :page-sizes="[100, 200, 300, 400]"
                :page-size="100"
                layout="total, sizes, prev, pager, next, jumper"
                :total="400"
              ></el-pagination>
            </el-tab-pane>
            <el-tab-pane label="" name="second">
              <el-table
                ref="multipleTable"
                :data="tableData"
                tooltip-effect="dark"
                style="width: 100%"
                @selection-change="handleSelectionChange"
              >
                <el-table-column type="selection" width="100"></el-table-column>
                <el-table-column prop="name" label="ID"></el-table-column>
                <el-table-column prop="name" label=""></el-table-column>
                <el-table-column prop="address" label="" show-overflow-tooltip></el-table-column>
                <el-table-column prop="address" label="" show-overflow-tooltip></el-table-column>
                <el-table-column prop="address" label="" show-overflow-tooltip></el-table-column>
                <el-table-column prop="address" sortable label="" show-overflow-tooltip></el-table-column>
                <el-table-column label="">
                  <template slot-scope="scope">
                    <el-button @click="handleClick(scope.row)" type="text" size="small"></el-button>
                    <el-button type="text" @click="keep" size="small"></el-button>
                  </template>
                </el-table-column>
              </el-table>
              <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="currentPage"
                :page-sizes="[100, 200, 300, 400]"
                :page-size="100"
                layout="total, sizes, prev, pager, next, jumper"
                :total="400"
              ></el-pagination>
            </el-tab-pane>
            <el-tab-pane label="" name="third">
              <el-table
                ref="multipleTable"
                :data="tableData"
                tooltip-effect="dark"
                style="width: 100%"
                @selection-change="handleSelectionChange"
              >
                <el-table-column type="selection" width="100"></el-table-column>
                <el-table-column prop="name" label="ID"></el-table-column>
                <el-table-column prop="name" label=""></el-table-column>
                <el-table-column prop="address" label="" show-overflow-tooltip></el-table-column>
                <el-table-column prop="address" label="" show-overflow-tooltip></el-table-column>
                <el-table-column prop="address" label="" show-overflow-tooltip></el-table-column>
                <el-table-column prop="address" sortable label="" show-overflow-tooltip></el-table-column>
                <el-table-column label="">
                  <template slot-scope="scope">
                    <el-button @click="handle(scope.row)" type="text" size="small"></el-button>
                    <el-button type="text" size="small"></el-button>
                  </template>
                </el-table-column>
              </el-table>
              <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="currentPage"
                :page-sizes="[100, 200, 300, 400]"
                :page-size="100"
                layout="total, sizes, prev, pager, next, jumper"
                :total="400"
              ></el-pagination>
            </el-tab-pane>
            <el-tab-pane label="" name="fourth">
              <el-table
                ref="multipleTable"
                :data="tableData"
                tooltip-effect="dark"
                style="width: 100%"
                @selection-change="handleSelectionChange"
              >
                <el-table-column type="selection" width="100"></el-table-column>
                <el-table-column prop="name" label="ID"></el-table-column>
                <el-table-column prop="name" label=""></el-table-column>
                <el-table-column prop="address" label="" show-overflow-tooltip></el-table-column>
                <el-table-column prop="address" label="" show-overflow-tooltip></el-table-column>
                <el-table-column prop="address" label="" show-overflow-tooltip></el-table-column>
                <el-table-column prop="address" sortable label="" show-overflow-tooltip></el-table-column>
                <el-table-column label="">
                  <template slot-scope="scope">
                    <el-button @click="handleClick(scope.row)" type="text" size="small"></el-button>
                  </template>
                </el-table-column>
              </el-table>
              <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="currentPage"
                :page-sizes="[100, 200, 300, 400]"
                :page-size="100"
                layout="total, sizes, prev, pager, next, jumper"
                :total="400"
              ></el-pagination>
            </el-tab-pane>
          </el-tabs>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      checkList: [" A"],
      dialogVisible: false,
      activeName: "first",
      currentPage: 4,
      tableData: [
        {
          date: "2016-05-03",
          name: "",
          address: " 1518 "
        },
        {
          date: "2016-05-02",
          name: "",
          address: " 1518 "
        },
        {
          date: "2016-05-04",
          name: "",
          address: " 1518 "
        },
        {
          date: "2016-05-01",
          name: "",
          address: " 1518 "
        },
        {
          date: "2016-05-08",
          name: "",
          address: " 1518 "
        },
        {
          date: "2016-05-06",
          name: "",
          address: " 1518 "
        },
        {
          date: "2016-05-07",
          name: "",
          address: " 1518 "
        }
      ],
      multipleSelection: []
    };
  },
  methods: {
    keep() {
      this.dialogVisible = true;
    },
    handleClick(tab, event) {
      console.log(tab, event);
     this.$router.push({path:'/Bteam/Verify'})
    },
    handle(tab, event) {
      console.log(tab, event);
     this.$router.push({path:'/Bteam/Enterdetail'})
    },
    toggleSelection(rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    handleSizeChange(val) {
      console.log(` ${val} `);
    },
    handleCurrentChange(val) {
      console.log(`: ${val}`);
    }
  },
  mounted: function() {},
  updated: function() {}
};
</script>
<style scoped>
.asp {
  width: 100%;
}
.asp-nav {
  margin: 10px 0 0 0;
  font-size: 17px;
  font-weight: 550;
}
.asp-content {
  width: 100%;
  margin: 20px 0 0 0;
  border: 1px solid #efeff3;
  background: #efeff3;
}
.asp-form {
  width: 98%;
  background: #ffffff;
  margin: 10px 0 0 1%;
}
.el-form-item {
  padding: 10px 0 0 20px;
}
.el-select {
  width: 203px;
}
.asp-table {
  width: 98%;
  background: #ffffff;
  margin: 10px 0 0 1%;
}
.el-pagination {
  margin: 20px 0 0 400px;
  padding: 0 0 20px 0;
}
.el-checkbox__label {
  font-size: 10px;
}
.el-checkbox {
  margin: 10px 0 0 20px;
}
</style>



