<template>
  <div class="asp">
    <div class="asp-nav"></div>
    <div class="asp-content">
      <div class="asp-form">
        <div></div>
        <div></div>
        <div></div>
        <div style="width:700px">
          
          <span>
            
            
            
            
          </span>
        </div>
        <div v-if="isshow">
            <el-button style="margin:0 0 0 550px" type="primary"></el-button>
            <el-button type="primary"></el-button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
        isshow:true,
        message:'1'
    };
  },
  methods: {},
  mounted: function() {},
  updated: function() {},
  created() {
      if(this.message == '1') {
          this.isshow = true
      }else{
          this.isshow = false
      }
  }
};
</script>
<style scoped>
.asp {
  width: 100%;
}
.asp-nav {
  margin: 10px 0 0 0;
  font-size: 17px;
  font-weight: 550;
}
.asp-content {
  width: 100%;
  margin: 20px 0 0 0;
  border: 1px solid #efeff3;
  background: #efeff3;
}
.asp-form {
  width: 98%;
  background: #ffffff;
  margin: 10px 0 10px 1%;
}
.asp-form div:nth-child(1) {
  padding: 50px 0 20px 50px;
}
.asp-form div {
  padding: 10px 0 20px 50px;
}
</style>


